body {
  position: relative;
  overflow-x: hidden;
  background: #f4f4f4;
}
body.fix {
  overflow: hidden;
  height: 100vh;
}

html {
  position: relative;
  overflow-x: hidden;
}

.container {
  max-width: 1640px;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

.title {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 48px;
  line-height: 1.2;
  color: #323232;
  margin: 0 0 30px;
}
.title__row {
  margin: 0 0 30px;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 10px;
  align-items: center;
}
.title__row .title {
  margin: 0;
}

.btn__green {
  border-radius: 30px;
  padding: 10px 30px;
  background: #109548;
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  text-transform: uppercase;
  color: #fff;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  transition: all 0.3s;
}
.btn__green:hover {
  box-shadow: 0 0 20px 0 rgba(97, 189, 86, 0.3);
  background: #61bd56;
}
.btn__green-arrow {
  border-radius: 5px;
  padding: 3px 15px 3px 20px;
  gap: 5px;
  font-size: 16px;
  line-height: 1.3;
  text-transform: none;
}

.video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  background: rgba(97, 189, 86, 0.3);
  backdrop-filter: blur(10px);
  border-radius: 100%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s;
  opacity: 0;
  pointer-events: none;
  z-index: 4;
}
.video-btn.active {
  opacity: 1;
  pointer-events: all;
}

.nav-menu {
  display: none;
}

.nav {
  position: relative;
  z-index: 10;
}
.nav__hum {
  display: none;
}
.nav__info {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 5px 0 0;
}
.nav__map {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 5px;
}
.nav__map img {
  width: 36px;
}
.nav__map p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fff;
}
.nav__map p span {
  font-weight: 700;
}
.nav__map + .nav__map {
  margin: 0 30px;
}
.nav__soc {
  margin: 0 50px 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.nav__soc svg,
.nav__soc img {
  width: 20px;
  height: 20px;
  object-fit: contain;
}
.nav__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.nav__phone a {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.3;
  color: #fff;
  transition: all 0.3s;
}
.nav__phone a:hover {
  color: #61bd56;
}
.nav__phone p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #fff;
}
.nav__row {
  border-radius: 30px;
  margin: 5px 0 0;
  padding: 14px 15px;
  margin: 5px 0 0;
  display: flex;
  justify-content: start;
  align-items: center;
  position: relative;
  z-index: 3;
}
.nav__row .btn__green {
  z-index: -1;
  position: relative;
}
.nav__row::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  inset: 0;
  backdrop-filter: blur(40px);
  background: rgba(255, 255, 255, 0.05);
  position: absolute;
  border-radius: 30px;
  z-index: -1;
}
.nav__row_overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: -1;
  background: #000;
  opacity: 0;
  transition: all 0.3s;
  pointer-events: none;
}
.nav__row_overflow.active {
  opacity: 0.5;
  pointer-events: all;
}
.nav__logo {
  max-width: 221px;
  display: block;
  position: relative;
  z-index: -1;
}
.nav__logo img {
  width: 100%;
}
.nav__list {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.nav__list a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 38px;
  padding: 0 10px;
  border-radius: 5px;
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #fff;
  transition: all 0.3s;
}
.nav__list a path,
.nav__list a svg {
  transition: all 0.3s;
}
.nav__list a:hover, .nav__list a.active {
  background: #fff;
  color: #109548;
}
.nav__list a:hover path, .nav__list a.active path {
  fill: #109548;
}
.nav__list a.active svg {
  transform: rotate(180deg);
}
.nav__list_drop {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  border-radius: 5px;
  padding: 10px 0px;
  backdrop-filter: blur(40px);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
  transition: all 0.3s;
  pointer-events: none;
  opacity: 0;
  z-index: 3;
}
.nav__list_drop a {
  padding: 10px 20px;
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #323232;
  display: block;
  transition: all 0.3s;
  display: block;
  border-radius: 0;
  width: 100%;
  min-width: max-content;
}
.nav__list_drop a:hover {
  background: #eff8ee;
}
.nav__list_btn {
  position: relative;
  z-index: 5;
}
.nav__list_btn.active .nav__list_drop,
.nav__list_btn.active .nav__service {
  opacity: 1;
  pointer-events: all;
}
.nav__list_service {
  position: static;
}
.nav__search {
  margin: 0 16px 0 0;
}
.nav__search_btn {
  cursor: pointer;
}
.nav__search_input {
  position: absolute;
  z-index: 5;
  left: 250px;
  right: 195px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.nav__search_input.active {
  opacity: 1;
  pointer-events: all;
}
.nav__search_input input {
  position: relative;
  width: 100%;
  border-radius: 5px;
  padding: 11px 50px;
  background: #fff;
  border: none;
  box-sizing: border-box;
}
.nav__search_input button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
  background: transparent;
  border: none;
  padding: 0;
  width: auto;
  height: 20px;
}
.nav__search_input button svg path {
  transition: all 0.3s;
}
.nav__search_input button:hover path {
  fill: #109548;
}
.nav__search_input ul {
  position: absolute;
  top: calc(100% + 10px);
  width: 100%;
  left: 0;
  padding: 15px 20px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 15px;
  border-radius: 5px;
  background: #fff;
}
.nav__search_input ul a {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #323232;
  transition: all 0.3s;
}
.nav__search_input ul a span {
  font-weight: 700;
}
.nav__search_input ul a:hover {
  color: #109548;
}
.nav__search_clear {
  display: block;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20px;
}
.nav__service {
  width: 1600px;
  max-width: calc(100vw - 20px);
  position: absolute;
  top: calc(100% + 5px);
  left: 50%;
  transform: translateX(-50%);
  border-radius: 30px;
  padding: 10px;
  backdrop-filter: blur(40px);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
  background: rgba(188, 190, 192, 0.2);
  z-index: 3;
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s;
}
.nav__service_tab {
  display: flex;
  justify-content: start;
  align-items: end;
}
.nav__service_tab li {
  width: 410px;
  padding: 27px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px 30px 0 0;
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.3;
  text-transform: uppercase;
  color: #fff;
  transition: all 0.3s;
  cursor: pointer;
}
.nav__service_tab li:hover, .nav__service_tab li.active {
  background: rgba(255, 255, 255, 0.8);
  color: #109548;
}
.nav__service_wrapper {
  display: none;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  padding: 20px;
  border-radius: 0 30px 30px 30px;
  background: rgba(255, 255, 255, 0.8);
}
.nav__service_wrapper.active {
  display: grid;
}
.nav__service .nav__service_item, .nav__service_item {
  display: grid;
  position: relative;
  overflow: hidden;
  border: 1px solid #cfeada;
  border-radius: 30px;
  padding: 20px;
  background: #fff;
  display: block;
  min-height: 160px;
  z-index: 2;
  transition: all 0.3s;
}
.nav__service .nav__service_item p, .nav__service_item p {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.3;
  color: #323232;
  transition: all 0.3s;
}
.nav__service .nav__service_item p + p, .nav__service_item p + p {
  white-space: normal;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.3;
  color: #707070 !important;
  margin: 5px 0 0;
  overflow: hidden;
  position: relative;
  text-overflow: ellipsis;
  display: block;
  word-break: break-all;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -ms-box-orient: vertical;
  box-orient: vertical;
  -webkit-line-clamp: 4;
  -moz-line-clamp: 4;
  -ms-line-clamp: 4;
  line-clamp: 4;
}
.nav__service .nav__service_item img, .nav__service_item img {
  position: absolute;
  right: 10px;
  bottom: 13px;
  width: 100px;
  height: 100px;
  object-fit: contain;
  z-index: -1;
}
.nav__service .nav__service_item:hover, .nav__service_item:hover {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.05);
  border: 1px solid #fff;
}
.nav__service .nav__service_item:hover p, .nav__service_item:hover p {
  color: #109548;
}

.header {
  position: relative;
  min-height: 100vh;
  z-index: 1;
  padding: 0 0 60px;
  display: grid;
  grid-template-rows: auto 1fr;
  gap: 60px;
}
.header::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background: #000;
  opacity: 0.5;
}
.header__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -2;
  pointer-events: none;
}
.header__block {
  margin: auto 0;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 420px;
  gap: 60px;
  align-items: start;
}
.header__container {
  display: flex;
}
.header__info {
  max-width: 1036px;
}
.header__info-vacancy {
  margin: auto 0;
}
.header__info-about {
  margin: auto 0 0;
}
.header__info h1 {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 128px;
  line-height: 1;
  color: #fff;
}
.header__info p {
  font-family: "AvantGardeCTT", sans-serif;
  margin: 70px 0 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.2;
  color: #fff;
}
.header__stocks {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  margin: 34px 0 0;
}
.header__stocks_mob {
  display: none;
}
.header__stocks_name {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.3;
  color: #fff;
  margin: 20px 0 0;
}
.header__stocks_img {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  top: 0;
  left: 0;
  transition: all 0.7s;
}
.header__stocks_date {
  border-radius: 5px;
  padding: 5px 10px;
  width: max-content;
  background: rgba(16, 149, 72, 0.2);
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #fff;
  transition: all 0.3s;
}
.header__stocks_text {
  margin: 8px 0 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.3;
  color: #fff;
}
.header__stocks_bold {
  margin: 12px 0 0;
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.3;
  color: #fff;
}
.header__stocks_link {
  position: absolute;
  bottom: 15px;
  right: 20px;
  border-radius: 100%;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  fill: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(40px);
  transition: all 0.3s;
}
.header__stocks_item {
  min-height: 200px;
  border-radius: 30px;
  backdrop-filter: blur(40px);
  background: rgba(255, 255, 255, 0.1);
  position: relative;
  padding: 20px 20px 50px;
  z-index: 2;
  overflow: hidden;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}
.header__stocks_item:hover .header__stocks_link, .header__stocks_item:hover .header__stocks_date {
  background: #61bd56;
}
.header__stocks_item:hover .header__stocks_img {
  transform: scale(1.07);
}
.header-about {
  margin: auto 0 0;
  width: 100%;
}
.header-about .about-network__har {
  margin-bottom: 0;
}
.header-about .about-network__har_item {
  backdrop-filter: blur(40px);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  gap: 10px;
}
.header-about .about-network__har_item p {
  color: #fff;
}

.about-network {
  padding: 60px 0 0;
}
.about-network__block {
  display: grid;
  grid-template-columns: calc(50% - 10px) calc(50% - 10px);
  gap: 20px;
  align-items: start;
}
.about-network__info h2 {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 1.2;
  color: #323232;
}
.about-network__info h2 span {
  color: #109548;
}
.about-network__info .btn__green {
  margin: 40px 0 0;
}
.about-network__text {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.about-network__text p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.3;
  color: #707070;
}
.about-network__bg {
  border-radius: 30px;
  width: 100%;
  min-height: 100%;
  object-fit: cover;
}
.about-network__har {
  margin: 60px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}
.about-network__har_item {
  border-radius: 30px;
  padding: 20px;
  background: #fff;
  padding: 15px 20px;
  display: flex;
  justify-content: start;
  align-items: center;
}
.about-network__har_item img,
.about-network__har_item svg {
  width: 70px;
  height: 70px;
}
.about-network__har_item p {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #323232;
}
.about-network__har_item p span {
  font-weight: 700;
}
.about-network__quote {
  border-radius: 30px;
  padding: 20px;
  background: #fff;
  height: 100%;
  display: grid;
  grid-template-columns: 34fr 39fr;
  gap: 20px;
}
.about-network__quote img {
  width: 100%;
  height: 1px;
  min-height: 100%;
  object-fit: cover;
  border-radius: 30px;
}
.about-network__quote_col {
  padding: 40px 0;
  display: flex;
  flex-direction: column;
}
.about-network__quote_text {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.3;
  color: #323232;
  margin: 0 0 auto;
}
.about-network__quote_info p {
  margin: 8px 0 0;
  line-height: 1.3;
}
.about-network__quote_info p:nth-child(1) {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #61bd56;
}
.about-network__quote_info p:nth-child(2) {
  font-family: "AvantGardeCTT", sans-serif;
  font-weight: 700;
  font-size: 24px;
  color: #323232;
}
.about-network__quote_info p:nth-child(3) {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #707070;
}
.about-network__quote .about-network__qoute_prev .about-network__quote_info {
  display: none;
}

.about-vacancy .about-network__har {
  margin-top: 0;
}

.service {
  padding: 60px 0 0;
}
.service__title {
  display: none;
}
.service .nav__service_tab li {
  color: #323232;
}
.service .nav__service_tab li.active, .service .nav__service_tab li:hover {
  color: #109548;
}
.service .nav__service_item {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.05);
  background: #fff;
  border: none;
}
.service .nav__service_item span {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 100%;
}
.service .nav__service_item span path {
  transition: all 0.3s;
}
.service .nav__service_item p:nth-child(1) {
  max-width: calc(100% - 35px);
}
.service .nav__service_item img {
  transition: all 0.3s;
}
.service .nav__service_item:hover {
  background: #61bd56;
}
.service .nav__service_item:hover img {
  opacity: 0.2;
}
.service .nav__service_item:hover p {
  color: #fff;
}
.service .nav__service_item:hover p + p {
  color: #e8e8e8 !important;
}
.service .nav__service_item:hover span {
  background: #fff;
}
.service .nav__service_item:hover span path {
  fill: #61bd56;
}

.doctor-list {
  padding: 60px 0 0;
  position: relative;
  overflow: hidden;
}
.doctor-list::before {
  content: "";
  display: block;
  min-width: 160px;
  width: calc((100vw - 1600px) / 2);
  height: 100%;
  background: linear-gradient(90deg, rgba(244, 244, 244, 0) 0%, rgba(244, 244, 244, 0.49) 18.75%, #f4f4f4 96.63%);
  position: absolute;
  left: calc(50vw + 800px);
  top: 0;
  z-index: 5;
}
.doctor-list::after {
  content: "";
  display: block;
  min-width: 160px;
  width: calc((100vw - 1600px) / 2);
  height: 100%;
  background: linear-gradient(90deg, rgba(244, 244, 244, 0) 0%, rgba(244, 244, 244, 0.49) 18.75%, #f4f4f4 96.63%);
  position: absolute;
  right: calc(50vw + 800px);
  top: 0;
  z-index: 5;
  transform: scaleX(-1);
}
.doctor-list__swiper {
  overflow: visible;
}
.doctor-list__prev {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}
.doctor-list__prev img {
  width: 100%;
  height: 350px;
  object-fit: cover;
  transition: all 0.3s;
}
.doctor-list__prev_text {
  position: absolute;
  left: 7px;
  bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  right: 7px;
}
.doctor-list__prev_text p {
  border-radius: 5px;
  padding: 3px 15px 5px;
  backdrop-filter: blur(40px);
  background: rgba(255, 255, 255, 0.5);
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: #323232;
}
.doctor-list__info {
  background: rgba(255, 255, 255, 0.5);
  padding: 20px;
  height: 100%;
